<template>
    <div>
        <group-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                connectable
                deletable
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addToGroup="onAddToGroup"
                :_style="{stroke:'black'}">
            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :sub-style="{'font-weight': 'bold','font-size': '16'}"
                        :text="value.name">
                </text-element>

            </sub-elements>
        </group-element>

        <modeling-property-panel
                v-model="value"
                titleName="BoundedContext"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'
    import GroupElement from "../../../opengraph/shape/GroupElement";
    import ImageElement from "../../../opengraph/shape/ImageElement";

    export default {
        components: {ImageElement, GroupElement},
        mixins: [Element],
        name: 'bounded-context-definition',
        props: {},
        computed: {

            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.BoundedContext'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    description_text: '     \n.',
                    _type: this.className(),
                    name: 'BoundedContext' + x,
                    namePlural: '',
                    aggregates: [],
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 300,
                        'height': 300,
                        'style': JSON.stringify({})
                    },
                    drawer: false,
                    checkValue: false,
                    selected: false,
                    portGenerated: 0
                }
            }
        },
        data: function () {
            return {
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
            };
        },
        created: function () {

        },
        watch: {
            'value.name': function (newVal) {
                var me = this
                me.value.checkValue = true
            },
            "value.drawer": function (newVal) {
                var me = this
                if (newVal == false) {
                    if (me.value.checkValue) {
                        if (me.value.aggregates) {

                            me.value.aggregates.forEach(function (agg) {
                                agg.boundedContext = me.value.name

                                if (agg.events)
                                    agg.events.forEach(function (eve) {
                                        eve.boundedContext = me.value.name
                                    })

                                if (agg.commands)
                                    agg.commands.forEach(function (co) {
                                        co.boundedContext = me.value.name
                                    })

                                if (agg.policies)
                                    agg.policies.forEach(function (po) {
                                        po.boundedContext = me.value.name
                                    })

                                if (agg.viewes)
                                    agg.viewes.forEach(function (vi) {
                                        vi.boundedContext = me.value.name
                                    })
                            })

                        }
                        me.value.checkValue = false
                    }
                }
            }

        },
        mounted: function () {
            var me = this
            var boundedId = this.value.elementView.id

            var $tr = $('#' + boundedId);
            $tr.parent().children().first().before($tr)


            var me = this
            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                if (obj.state == 'deleteAgg') {
                    // // console.log(me.value)
                    me.value.aggregates.some(function (item, idx) {
                        if (item.elementView.id == obj.element.elementView.id) {
                            me.value.aggregates[idx] = null
                            me.value.aggregates = me.value.aggregates.filter(n => n);
                        }
                    })
                }
                if (obj.state == 'addAgg') {
                    // console.log(me.value)
                    me.value.aggregates.push(obj.element)
                }
                if (obj.state == 'updateAgg') {
                    me.value.aggregates.some(function (item, idx) {
                        me.value.aggregates[idx] = obj.element
                    })
                }
            })
        },
        methods: {
            deleteBoundedContext() {
                var me = this
                return new Promise(function (resolve) {
                    me.deleteInBounded(me.value.name);
                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    me.$emit('update:value', null);
                    resolve()
                })
            },
            deleteInBounded(boundedName) {
                var me = this
                var designer = this.getComponent('modeling-designer')

                designer.value.definition.forEach(function (item, idx) {
                    if (item != null) {
                        if (item.boundedContext && item.boundedContext.name == boundedName) {
                            // console.log("boundedItem Id : ", item.elementView.id)
                            me.$EventBus.$off(`${item.elementView.id}`);
                            designer.value.definition[idx] = null;
                        }
                    }
                })
                // designer.value.definition = designer.value.definition.filter(n => n);
            }
        }
    }
</script>


