<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                deletable
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:rotateShape="onRotateShape"
                v-on:addedToGroup="onAddedToGroup"
                :label.sync="value.name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5FC08B',
          fill: '#5FC08B',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< View >>'">
                </text-element>
                <image-element
                        v-if="value.name.length < 1 || value.viewFieldDescriptors.length <1"
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <modeling-property-panel
                v-model="value"
                :titleName="'View'"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/view.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    export default {
        mixins: [Element],
        name: 'view-definition',
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.View'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    description_text: '      \n' +
                        '(   :  CQRS  )',
                    _type: this.className(),
                    name: '',
                    aggregate: {},
                    namePascalCase: '',
                    namePlural: '',
                    boundedContext: {},
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({})
                    },
                    checkValue: false,
                    drawer: false,
                    selected: false,
                    editing: false,
                    editingView: false,
                    dataProjection: '',
                    isCqrs: false,
                    isMash: false,
                    isgraph: false,
                    viewHeaders: [],
                    viewFieldDescriptors: [],
                    fieldDescriptorsCqrs: [],
                    fieldDescriptorsMash: [],
                    events: []
                }
            }
        },
        data: function () {
            return {
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
            };
        },
        created: function () {

        },
        watch: {
            "value.events": function () {
                this.value.checkValue = true
            },
            "value.viewFieldDescriptors": function () {
                this.value.checkValue = true
            },
            "value.boundedContext": function () {
                this.value.checkValue = true
            },
            "value.name": function () {
                this.value.checkValue = true
            },
            "value.aggregate": {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me = this
                    if (newVal.id == oldVal.id) {
                        // console.log("Equal")
                    } else {
                        // console.log("no Equal")
                        var addObj = {
                            state: "addView",
                            element: me.value
                        }
                        var deleteObj = {
                            state: "deleteView",
                            element: me.value
                        }
                        if (oldVal.length < 1) {
                            me.$EventBus.$emit(oldVal.id, deleteObj)
                        }
                        me.$EventBus.$emit(newVal.id, addObj)

                    }
                }
            },
            "value.drawer": _.debounce(
                function (newVal, oldVal) {
                    var me = this
                    if (newVal == false) {
                        var obj = {
                            state: "updateView",
                            element: me.value
                        }
                        // console.log(me.value.aggregate)
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                        me.$EventBus.$emit('storage')
                    }
                }, 500),
            "value.dataProjection": function (newVal, oldVal) {
                var me = this
                // console.log(newVal)
                if (newVal == 'cqrs') {
                    me.value.isCqrs = true
                    me.value.isMash = false
                    me.value.isgraph = false
                    me.value.viewHeaders = [
                        {
                            text: 'Key',
                            align: 'left',
                            sortable: false,
                            value: 'isKey',
                        },
                        {text: 'name', value: 'name'},
                        {text: 'type', value: 'className'},
                        {text: 'sourceEvent', value: 'sourceEvent'},
                        {text: 'sourceEvent or DirectValue', value: 'eventDirectValue'},
                        {text: 'viewColumnName', value: 'viewColumnName'},
                        {text: 'sourceEventColumn', value: 'sourceEventColumn'},
                        {text: 'Actions', value: 'action'},
                    ]
                    me.value.viewFieldDescriptors = me.value.fieldDescriptorsCqrs
                } else if (newVal == 'uimashup') {
                    me.value.isCqrs = false
                    me.value.isMash = true
                    me.value.isgraph = false
                    me.value.viewHeaders = [
                        {
                            text: 'Key',
                            align: 'left',
                            sortable: false,
                            value: 'isKey',
                        },
                        {text: 'name', value: 'name'},
                        {text: 'type', value: 'className'},
                        {text: 'sourceRepository', value: 'sourceRepository'},
                        {text: 'sourceRepository or DirectValue', value: 'repositoryDirectValue'},
                        {text: 'hateoas', value: 'hateoas'},
                        {text: 'link', value: 'link'},
                        {text: 'Actions', value: 'action'},
                    ]
                    me.value.viewFieldDescriptors = me.value.fieldDescriptorsMash
                } else if (newVal == 'graphql') {
                    me.value.isCqrs = false
                    me.value.isMash = false
                    me.value.isgraph = true
                }
            }
        },
        mounted: function () {
            var me = this
            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {


                if (obj.state == 'delete') {

                    if (obj.element._type == "org.uengine.modeling.model.Aggregate") {
                        me.value.aggregate = {}
                    }

                } else if (obj.state == 'modify') {

                }


                // if (obj.state == "delete" && obj.element._type == "org.uengine.modeling.model.Aggregate") {
                //     me.value.aggregate = {}
                // } else if (obj.element._type == me.value._type) {
                //     //aggregate
                //     if (me.value.aggregate) {
                //         me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                //     }
                //
                //     me.deleteActivity(obj)
                //
                // }

                me.$nextTick(function () {
                    var designer = me.getComponent('modeling-designer')
                    designer.editing = true;
                })
            })


            var tmp = me.value.dataProjection;
            // me.value.dataProjection = '';
            this.$nextTick(function () {
                if (tmp == 'cqrs') {
                    // // console.log("")
                    // console.log(me.value.dataProjection)

                    me.value.viewHeaders = [
                        {
                            text: 'Key',
                            align: 'left',
                            sortable: false,
                            value: 'isKey',
                        },
                        {text: 'columnName', value: 'name'},
                        {text: 'type', value: 'className'},
                        {text: 'sourceEvent', value: 'sourceEvent'},
                        {text: 'sourceEvent or DirectValue', value: 'eventDirectValue'},
                        {text: 'viewColumnName', value: 'viewColumnName'},
                        {text: 'sourceEventColumn', value: 'sourceEventColumn'},
                        {text: 'Actions', value: 'action'},
                    ]
                    me.value.viewFieldDescriptors = me.value.fieldDescriptorsCqrs
                } else if (tmp == 'uimashup') {
                    // console.log(me.value.dataProjection)

                    me.value.viewHeaders = [
                        {
                            text: 'Key',
                            align: 'left',
                            sortable: false,
                            value: 'isKey',
                        },
                        {text: 'columnName', value: 'name'},
                        {text: 'type', value: 'className'},
                        {text: 'sourceRepository', value: 'sourceRepository'},
                        {text: 'sourceRepository or DirectValue', value: 'repositoryDirectValue'},
                        {text: 'hateoas', value: 'hateoas'},
                        {text: 'link', value: 'link'},
                        {text: 'Actions', value: 'action'},
                    ]

                    me.value.viewFieldDescriptors = me.value.fieldDescriptorsMash
                } else if (tmp == 'graphql') {
                    // console.log(me.value.dataProjection)

                }
            })
        },
        methods: {
            deleteView() {
                var me = this

                return new Promise(function (resolve) {
                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


<style scoped lang="scss" rel="stylesheet/scss">

</style>
