<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                connectable
                deletable
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:addedToGroup="onAddedToGroup"
                :label="value.name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#BB94BF',
          fill: '#BB94BF',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <!--title-->
                <image-element
                        v-if="(Object.keys(value.aggregate).length === 0 && JSON.stringify(value.aggregate) === JSON.stringify({})) || value.name.length < 1"
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Policy >>'">
                </text-element>

            </sub-elements>
        </geometry-element>


        <modeling-property-panel
                v-model="value"
                titleName="Policy"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/policy.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'policy-definition',
        props: {},
        computed: {
            namePascalCase() {
                var me = this
                return me.name.charAt(0).toUpperCase() + me.name.slice(1)
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Policy'
            },
            createNew(elementId, x, y, width, height) {

                return {
                    description_text: '\n' +
                        '   (subscribe)\n',
                    _type: this.className(),
                    name: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    namePlural: '',
                    aggregate: {},
                    boundedContext: {},
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({})
                    },
                    checkValue: false,
                    drawer: false,
                    selected: false,
                    editing: false,
                    relationEventInfo: [],
                }
            }
        },
        data: function () {
            return {
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
            };
        },
        created: function () {

        },
        watch: {
            "value.boundedContext": function () {
                this.value.checkValue = true
            },
            "value.name": function () {
                this.value.checkValue = true
            },
            "value.aggregate": {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me= this
                    if(newVal.id == oldVal.id) {
                        // console.log("Equal")
                    } else {
                        // console.log("no Equal")
                        var addObj = {
                            state: "addPolicy",
                            element: me.value
                        }
                        var deleteObj = {
                            state: "deletePolicy",
                            element: me.value
                        }
                        if (oldVal.length < 1) {
                            me.$EventBus.$emit(oldVal.id, deleteObj)
                        }
                        me.$EventBus.$emit(newVal.id, addObj)

                    }
                }
            },
            "value.drawer": _.debounce(
                function (newVal, oldVal) {
                    var me = this
                    if (newVal == false) {
                        var obj = {
                            state: "updatePolicy",
                            element: me.value
                        }
                        // console.log("aa")
                        // console.log(me.value.aggregate)
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                        me.$EventBus.$emit('storage')
                    }
                }, 500)

        },
        mounted: function () {
            var me = this
            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                if(obj.state == 'addRelation') {
                    // console.log(obj.element)
                    var checked = false;
                    me.value.relationEventInfo.some(function (eventItem, evtIdx) {
                        var evObj = {
                            eventValue: JSON.parse(JSON.stringify(obj.element.sourceElement)),
                            relationType: obj.element.relationValue
                        }
                        if(eventItem.eventValue.elementView.id == obj.element.sourceElement.elementView.id) {
                            me.value.relationEventInfo[evtIdx] = evObj;
                            checked = true;
                        }
                    })
                    if(!checked) {
                        var evObj = {
                            eventValue: JSON.parse(JSON.stringify(obj.element.sourceElement)),
                            relationType: obj.element.relationValue
                        }
                        me.value.relationEventInfo.push(evObj)
                    }

                    me.$EventBus.$emit(me.value.aggregate.id,  {state: "updateAgg"})
                }
                if(obj.state == 'updateEvent') {
                    // console.log(obj.element)
                    // var evObj = {
                    //     eventValue: JSON.parse(JSON.stringify(obj.element.sourceElement)),
                    //     relationType: obj.element.relationValue
                    // }
                    me.value.relationEventInfo.forEach(function (eventItem, evtIdx) {
                        if(eventItem.eventValue.elementView.id == obj.element.elementView.id) {
                            me.value.relationEventInfo[evtIdx].eventValue = JSON.parse(JSON.stringify(obj.element));
                        }
                    })
                    var aggObj = {
                        state: "updateAgg",
                    }
                    me.$EventBus.$emit(me.value.aggregate.elementView.id,  {state: "updateAgg"})
                }
            })
        },
        methods: {
            deletePolicy() {
                var me = this

                return new Promise(function (resolve) {

                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


