<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div class="canvas-panel">
        <!-- Code Modal -->
        <v-layout right>
            <opengraph ref="opengraph" focus-canvas-on-select wheelScalable :labelEditable="false"
                       :dragPageMovable="dragPageMovable" :enableContextmenu="false" :enableRootContextmenu="false"
                       :enableHotkeyCtrlC="false" :enableHotkeyCtrlV="false"
                       :enableHotkeyDelete="false" :enableHotkeyCtrlZ="false" :enableHotkeyCtrlD="false"
                       :enableHotkeyCtrlG="false" :slider="true" :movable="false" :resizable="true" :selectable="true"
                       :deletable="false"
                       :connectable="false" v-if="value" v-on:canvasReady="bindEvents" :autoSliderUpdate="true"
                       :imageBase="imageBase">
                <!---->
                <div v-for="(element, index) in value.definition">
                    <component :is="getComponentByClassName(element._type)"
                               :value.sync="value.definition[index]"></component>
                </div>
                <div v-for="(element, index) in value.relation">
                    <component :is="getComponentByClassName(element._type)"
                               :value.sync="value.relation[index]"></component>
                </div>
            </opengraph>

            <v-layout right>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>

                <v-row style="margin-top: 10px; margin-right: 5px; width: 30%" justify="end" align="start">
                    <v-text-field
                            :width="300"
                            disabled
                            label="Project Name" v-model="projectName"
                            @click="unselectedAll">
                    </v-text-field>
                </v-row>
            </v-layout>


            <v-card class="tools" style="top:100px; text-align: center;">
                <span class="bpmn-icon-hand-tool" v-bind:class="{ icons : !dragPageMovable, hands : dragPageMovable }"
                      _width="30"
                      _height="30" v-on:click="toggleGrip">
                     <v-tooltip md-direction="right">Hands</v-tooltip>
                </span>
            </v-card>

        </v-layout>

        <modeler-image-generator ref="modeler-image-generator"></modeler-image-generator>

    </div>
</template>

