            unselectedAll: function (newVal) {
                // console.log(newVal)
                this.value.definition.forEach(function (definition) {
                    definition.selected = false
                })
                this.value.relation.forEach(function (relation) {
                    relation.selected = false
                })
            },
            ajax: function (url, method, payload, successCallback) {
                var xhr = new XMLHttpRequest();
                xhr.open(method, url, true);
                // xhr.withCredentials = true;
                xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
                xhr.onreadystatechange = function () {
                    if (xhr.readyState != 4 || xhr.status != 200) return;
                    successCallback(xhr.responseText);
                };
                xhr.send(JSON.stringify(payload));
            },
            modelSetting() {
                var me = this

                me.fullPathList = []
                me.getCodeList = []
                me.treeList = []

                me.model = {
                    _type: "org.uengine.modeling.model.Model",
                    boundedContexts: [],
                    relations: [],
                    projectName: me.projectName
                }

                return new Promise(function (resolve) {
                    me.value.definition.forEach(function (item) {
                        if (item._type == 'org.uengine.modeling.model.BoundedContext') {
                            me.model.boundedContexts.push(item)
                        }
                    })

                    var modelForElements = {
                        BoundedContext: [],
                        AggregateRoot: [],
                        Aggregate: [],
                        Policy: [],
                        Event: [],
                        Command: [],
                        View: [],
                        // RelationEventInfo:[],
                    };

                    // 
                    me.model.relations = me.recursiveDeepCopy(me.value.relation)

                    //element 
                    me.model.boundedContexts.forEach(function (bound, idx) {
                        bound.portGenerated = 8081 + idx
                        modelForElements["BoundedContext"].push(bound);

                        me.model.boundedContexts[idx].aggregates.forEach(function (agg, idx) {
                            // fieldDescriptors 
                            agg.aggregateRoot.fieldDescriptors.forEach(function (fd) {
                                // fd.nameCamelCase = _.camelCase(fd.name)
                                // fd.namePascalCase = fd.nameCamelCase.substring(0, 1).toUpperCase() + fd.nameCamelCase.substring(1);
                                if (fd.isKey)
                                    agg.aggregateRoot.keyFieldDescriptor = fd;
                            })


                            modelForElements["Aggregate"].push(agg);
                            modelForElements["AggregateRoot"].push(agg.aggregateRoot);

                            if (agg.commands)
                                agg.commands.forEach(function (co) {
                                    if (co.name == '') {
                                        me.text = "Check Element Name"
                                        me.snackbar = true
                                        me.color = "error"
                                        me.timeout = 1000
                                        me.checkName = false
                                    }
                                    modelForElements["Command"].push(co);
                                })

                            if (agg.policies)
                                agg.policies.forEach(function (po) {
                                    if (po.name == '') {
                                        me.text = "Check Element Name"
                                        me.snackbar = true
                                        me.timeout = 1000
                                        me.color = "error"

                                        me.checkName = false
                                    }
                                    modelForElements["Policy"].push(po);
                                })

                            if (agg.events)
                                agg.events.forEach(function (ev) {
                                    if (ev.name == '') {
                                        me.text = "Check Element Name"
                                        me.snackbar = true
                                        me.timeout = 1000
                                        me.color = "error"
                                        me.checkName = false
                                    }
                                    modelForElements["Event"].push(ev);
                                })

                            if (agg.viewes)
                                agg.viewes.forEach(function (vi) {
                                    if (vi.name == '') {
                                        me.text = "Check Element Name"
                                        me.snackbar = true
                                        me.timeout = 1000
                                        me.color = "error"
                                        me.checkName = false
                                    }
                                    modelForElements["View"].push(vi);
                                })
                        })

                    });
                    resolve()
                })

            },
            restApiPush: function () {
                var me = this;
                me.$http.post(`http://localhost:8080/event/${me.projectName}`, me.value, {
                        responseType: "arraybuffer",
                        headers: {
                            'Content-Type': 'application/zip;'
                        }
                    }
                ).then(function (response) {
                    var blob = new Blob([response.data], {type: 'application/zip'});
                    var fileName = me.projectName + ".zip";
                    saveAs(blob, fileName);
                })
            },
            b64toBlob: function (b64Data, contentType, sliceSize) {
                contentType = contentType || '';
                sliceSize = sliceSize || 512;

                var byteCharacters = atob(b64Data);
                var byteArrays = [];

                for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
                    var slice = byteCharacters.slice(offset, offset + sliceSize);

                    var byteNumbers = new Array(slice.length);
                    for (var i = 0; i < slice.length; i++) {
                        byteNumbers[i] = slice.charCodeAt(i);
                    }

                    var byteArray = new Uint8Array(byteNumbers);

                    byteArrays.push(byteArray);
                }

                var blob = new Blob(byteArrays, {type: contentType});
                return blob;
            },
