<script>
    import firebase from 'firebase'

    export default {
        name: 'modeling-element-base',
        props: {
            value: Object,
        },
        created: function () {
            var me = this;
            me.messageRef = firebase.database().ref(`/${me.$route.params.author}/${me.$route.params.projectName}`);
        },
        data: function () {
            return {
                _id: null,
                rotateMove: false,
                tmpWidth: 0,
                tmpHeight: 0,
                messageRef: {},
                isException: 'NO',
            }
        },
        computed: {
            type() {
                return ''
            },
            code() {
                return ''
            },
            style: {
                get: function () {
                    var style;
                    //    .
                    if (this.value) {
                        if (this.value.elementView)
                            style = this.value.elementView.style;
                        else
                            style = this.value.relationView.style;
                    }

                    if (style) {
                        var jsonStyle = JSON.parse(style);
                        if ($.isEmptyObject(jsonStyle)) {
                            return this.defaultStyle;
                        } else {
                            return jsonStyle;
                        }
                    } else {
                        return this.defaultStyle;
                    }
                },
                set: function (val) {
                    if (this.value) {
                        if (this.value.elementView)
                            this.value.elementView.style = JSON.stringify(val);
                        else
                            this.value.relationView.style = JSON.stringify(val);
                    }
                }
            },
            // computedElement() {
            //     return _.cloneDeep(this.value)
            // }

        },
        watch: {
            // computedElement: {
            //     deep: true,
            //     handler:
            //         function (newVal, oldVal) {
            //             var diff = this.difference(newVal, oldVal)
            //
            //             if (typeof diff != 'undefined' && diff != {}) {
            //                 // // console.log(diff)
            //             var me = this
            //             var diff = this.difference(oldVal, newVal)
            //
            //             if (typeof diff != 'undefined' && diff != {}) {
            //                 if (diff.aggregate) {
            //                     // console.log(diff)
            //
            //                     var obj = {
            //                         state: "aggDelete",
            //                         element: me.value
            //                     }
            //                     me.$EventBus.$emit(`${diff.aggregate.id}`, obj)
            //                 }
            //             }
            //
            //         }
            // },
            value: {
                deep: true,
                handler:
                    function (newVal, oldVal) {
                        var me = this
                        var designer = me.getComponent('modeling-designer')

                        if (newVal.checkValue) {
                            if (!newVal.selected) {
                                if (!newVal.drawer) {
                                    if (!newVal.isUndoRedo) {
                                        // console.log("user - modify value ")
                                        // var obj = {
                                        //     state: "modify",
                                        //     element: newVal
                                        // }
                                        // me.$EventBus.$emit(`${newVal.elementView.id}`, obj)
                                        // me.$EventBus.$emit('storage')
                                        // me.$EventBus.$emit('undoRedo', obj)

                                    }
                                }
                            }
                        }
                    }
            },
            // "value.aggregate": {
            //     deep: true,
            //     handler(newVal, oldVal) {
            //         var me = this
            //         // console.log(newVal, oldVal)
            //         // console.log(newVal.id, oldVal.id)
            //         var deleteObj = {
            //             state: "aggDelete",
            //             element: me.value
            //         }
            //         var addObj = {
            //             state: "modify",
            //             element: me.value
            //         }
            //         me.$EventBus.$emit(oldVal.id, deleteObj)
            //         me.$EventBus.$emit(`${newVal.id}`, addObj)
            //
            //         if (me.value._type == '') {
            //
            //         }
            //     }
            // },
            "value.elementView.width": {
                handler: function (newVal, oldVal) {

                    var me = this
                    if (me.rotateMove == true) {
                        me.tmpWidth = oldVal
                    }
                }
            },
            "value.elementView.height":
                {
                    handler: function (newVal, oldVal) {
                        var me = this
                        if (me.rotateMove == true) {
                            me.tmpHeight = oldVal
                        }
                    }
                }
            ,
        },
        mounted: function () {
            var me = this
            $(document).keydown((evt) => {
                if (evt.keyCode == 46 || evt.keyCode == 8) {
                    if (me.value.selected && !me.value.drawer) {
                        var obj = {
                            state: 'delete',
                            element: me.value
                        }
                        if (me.value._type == 'org.uengine.modeling.model.Relation') {
                            me.$EventBus.$emit(`${me.value.relationView.id}`, obj)
                            // console.log("bus relation send: " + me.value.relationView.id)
                        } else {
                            me.$EventBus.$emit(`${me.value.elementView.id}`, obj)
                            // console.log("bus elemnet send: " + me.value.elementView.id)
                        }
                    }
                }
            });

            if (me.value._type != 'org.uengine.modeling.model.Relation') {
                me.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                    if (obj.state == "delete") {
                        me.deleteActivity(obj)
                    }
                })
            }


        },
        beforeDestroy() {
            if(this.value.elementView) {
                this.$EventBus.$off(`${this.value.elementView.id}`)
            } else {
                this.$EventBus.$off(`${this.value.relationView.id}`)
            }

        },
        methods: {
            mergeDeep(target, sources) {
                // console.log(target)
                if (!sources.length) return target;
                const source = sources.shift();

                if (isObject(target) && isObject(source)) {
                    for (const key in source) {
                        if (isObject(source[key])) {
                            if (!target[key]) Object.assign(target, {[key]: {}});
                            mergeDeep(target[key], source[key]);
                        } else {
                            Object.assign(target, {[key]: source[key]});
                        }
                    }
                }

                return this.mergeDeep(target, sources);
            },
        },
        methods: {
            deleteActivity(obj) {
                var me = this
                var designer = this.getComponent('modeling-designer')
                var opengraph = this.getComponent('opengraph')
                // console.log(this.value._type)
                var type = this.value._type.split(".");
                type = type[type.length - 1]
                eval("this.delete" + type + "()");

                if (!obj.element.isUndoRedo) {
                    me.value.selected = false;
                    //user delete
                    if (me.value._type != "org.uengine.modeling.model.Relation") {
                        // console.log("user-delete-element");
                        me.$EventBus.$emit('storage')
                        // me.$EventBus.$emit('undoRedo', obj)

                    } else {
                        //
                        // console.log("user-delete- relation")

                    }
                } else {
                    //undoRedo 
                    if (me.value._type != "org.uengine.modeling.model.Relation") {
                        // console.log("undoRedo-delete-element")

                        var totalLength = designer.value.definition.length
                        designer.value.definition.some(function (currValue, currIndex) {
                            if (!currValue) {
                                if (me.value.elementView.id === currValue.elementView.id) {
                                    designer.value.definition[currIndex] = null
                                    designer.value.definition[currIndex] = designer.value.definition[totalLength - 1];
                                    designer.value.definition[totalLength - 1] = null
                                    return;
                                }
                            }
                        })
                        // designer.value.definition = designer.value.definition.filter(n => n);

                    } else {
                        // console.log("undoRedo-delete-relation")
                    }

                }
            },
            difference: _.debounce(
                function (object, base) {
                    function changes(object, base) {
                        return _.transform(object, function (result, value, key) {
                            if (!_.isEqual(value, base[key])) {
                                result[key] = (_.isObject(value) && _.isObject(base[key])) ? changes(value, base[key]) : value;
                            }
                        });
                    }

                    return changes(object, base);
                }, 100
            ),
            onLabelChanged: function (be, af) {
                // console.log("onLabelChanged", be, af);
            },
            onRotateShape: function (me, angle) {
                this.value.elementView.angle = angle
            },
            connected: function () {
                // console.log(this.value)
            },
            selectedActivity: function () {
                if (this.value) {
                    this.value.selected = true
                }
                // this._selected = true;
            },
            deSelectedActivity: function () {
                // // console.log('UnSelected')
                if (this.value) {
                    this.value.selected = false
                    if (this.value.drawer) {
                        this.value.drawer = false
                    }
                }
            },
            showProperty: function () {
                var designer = this.getComponent('modeling-designer')
                if (!this.value.editing) {
                    this.value.drawer = true;
                } else {
                    designer.snackbar = true
                }

            },

            /**
             *     
             * @param groupElement
             * @param elements
             */
            onAddToGroup: function (groupElement, elements, eventOffset) {
                elements.forEach(function (element) {
                    var inner = false

                    groupElement.$parent.value.aggregates.some(function (tmp) {
                        if (tmp.elementView.id == element.$parent.value.elementView.id) {
                            return inner = true;
                        }
                    })

                    if (inner == false) {
                        if (groupElement.$parent.value._type == 'org.uengine.modeling.model.BoundedContext' && element.$parent.value._type == 'org.uengine.modeling.model.Aggregate') {
                            groupElement.$parent.value.aggregates.push(element.$parent.value)
                            // groupElement.$parent.value.boundedContext = element.$parent.value.name
                            // element.$parent.value.aggregates.push(groupElement.$parent.value)
                        }
                    }

                })
            },
            /**
             *     
             * @param groupElement
             * @param element
             */
            onAddedToGroup: function (groupElement, element, eventOffset) {
                var me = this;
                var designer = this.getComponent('modeling-designer')

                if (groupElement.tagName) {
                    // Canvas  
                    // console.log("Out BoundedContext")
                    designer.value.definition.forEach(function (tmp) {
                        // // console.log(tmp)
                        if (tmp != null)
                            if (tmp._type == 'org.uengine.modeling.model.BoundedContext' && tmp.name == element.$parent.value.boundedContext) {
                                tmp.aggregates.some(function (boundedTmp, idx) {
                                    if (boundedTmp.elementView.id == element.$parent.value.elementView.id) {
                                        tmp.aggregates = [
                                            ...tmp.aggregates.slice(0, idx),
                                            ...tmp.aggregates.slice(idx + 1)
                                        ]
                                    }
                                })
                            }
                    })

                    element.$parent.value.boundedContext = ""

                } else {
                    // console.log("Into BoundedContext")
                    // Bounded Context  
                    //     
                    if (groupElement.$parent.value._type == 'org.uengine.modeling.model.BoundedContext') {
                        var boundedOb = {
                            id: groupElement.$parent.value.elementView.id,
                            name: groupElement.$parent.value.name
                        }

                        element.$parent.value.boundedContext = {}
                        element.$parent.value.boundedContext = boundedOb
                    }
                }

            },
            uuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            },
            refresh() {
                var me = this
                me.value.elementView.x = me.value.elementView.x + 1
                me.$nextTick(function () {
                    me.value.elementView.width = me.tmpWidth
                    me.value.elementView.height = me.tmpHeight
                    me.rotateMove = true
                    me.value.elementView.x = me.value.elementView.x - 1
                    me.$nextTick(function () {
                        me.value.elementView.width = me.tmpWidth
                        me.value.elementView.height = me.tmpHeight
                    })
                })
            },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },
            // onRemoveShape: function (ele) {
            //     var me = this
            //     var opengraph = me.getComponent('opengraph')
            //     opengraph.removeElement(ele)
            //     // console.log("delete onRemoveShape")
            // }
        }
    }
</script>


