<template>
    <!-- width 390 -->
    <v-layout wrap>
        <v-navigation-drawer v-model="value.drawer" absolute right v-bind:style="{width: widthScale}">

            <!--       -->
            <v-list class="pa-1">
                <v-list-item>
                    <v-list-item-avatar>
                        <img :src="img">
                    </v-list-item-avatar>
                    <v-list-item-title class="headline">{{ titleName }}
                    </v-list-item-title>
                    <v-tooltip v-model="show" top>
                        <template v-slot:activator="{ on }">
                            <v-btn icon @click="value.drawer =false">
                                <v-icon color="grey lighten-1">mdi-close</v-icon>
                            </v-btn>
                            <v-btn icon v-on="on">
                                <v-icon color="grey lighten-1">mdi-information</v-icon>
                            </v-btn>
                        </template>
                        <span>{{value.description_text}}</span>
                    </v-tooltip>
                </v-list-item>
            </v-list>

            <!--      -->
            <v-list class="pt-0" dense flat>
                <v-divider></v-divider>
                <v-card v-if="value._type == 'org.uengine.modeling.model.Relation' && value.sourceElement._type == 'org.uengine.modeling.model.Event'">
                    <v-card-text>
                        <span class="headline">Type</span>
                        <v-layout>
                            <v-col>
                                <v-radio-group v-model="value.relationKey" style="padding-left: 10px;">
                                    <v-radio label="Event-Driven(Pub/Sub)" value="RestAPI"></v-radio>
                                    <v-radio label="Request & Response(Restful)" value="Restful"></v-radio>
                                </v-radio-group>
                                <v-select :disabled="value.relationKey == 'RestAPI'"
                                          v-model="value.relationValue"
                                          :items="restfulList"
                                          label="Request & Response(Restful)"></v-select>
                            </v-col>
                        </v-layout>
                    </v-card-text>
                </v-card>
                <v-card outlined v-else>
                    <v-card-text>
                        <span class="headline" v-if="titleName">{{titleName}} Name</span>
                        <v-textarea name="input-7-1" outline :label="'Input Text'" auto-grow
                                    v-model="value.name"></v-textarea>

                        <v-card outlined v-if="usedTranslate">
                            <v-card-text @click="changeTranslate()">
                                  : {{ translateText }}
                            </v-card-text>
                            <v-card-text>
                                  .
                            </v-card-text>
                        </v-card>


                        <span class="headline" v-if="value._type == 'org.uengine.modeling.model.Event'">Trigger</span>
                        <v-layout v-if="value._type == 'org.uengine.modeling.model.Event'" flat>
                            <v-radio-group v-model="value.trigger" style="font-size: small">
                                <v-row>
                                    <v-col>
                                        <v-radio label="Pre Persist" value="@PrePersist"></v-radio>
                                        <v-radio label="Pre Update" value="@PreUpdate"></v-radio>
                                        <v-radio label="Pre Delete" value="@PreDelete"></v-radio>
                                    </v-col>
                                    <v-col>
                                        <v-radio label="Post Persist" value="@PostPersist"></v-radio>
                                        <v-radio label="Post Update" value="@PostUpdate"></v-radio>
                                        <v-radio label="Post Delete" value="@PostDelete"></v-radio>
                                    </v-col>
                                </v-row>
                            </v-radio-group>
                        </v-layout>


                        <span class="headline"
                              v-if="value._type == 'org.uengine.modeling.model.Event' ||
                              value._type == 'org.uengine.modeling.model.Aggregate'">Attributes</span>
                        <v-layout
                                v-if="value._type == 'org.uengine.modeling.model.Event'"
                                flat>
                            <v-col>
                                <v-data-table
                                        :headers="headers"
                                        :items="fieldDescriptors"
                                        hide-default-header
                                        hide-default-footer
                                        class="elevation-1"
                                >

                                    <template v-slot:item.action="{ item }">
                                        <v-icon
                                                small
                                                @click="deleteEntity(item)"
                                        >
                                            delete
                                        </v-icon>
                                    </template>
                                </v-data-table>


                                <v-row justify="center">
                                    <v-flex xs4>
                                        <v-select v-model="entityType" :items="entityTypeList" label="Type"
                                                  style="margin-right: 15px;"></v-select>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-text-field v-model="entityName" label="Name"
                                                      v-on:keyup.enter="attributeADD(entityType,entityName)"
                                                      required></v-text-field>
                                    </v-flex>
                                </v-row>
                                <v-row justify="end">
                                    <v-btn v-if="value._type == 'org.uengine.modeling.model.Event'" text
                                           @click="syncFromAggregate()">
                                        <v-icon>mdi-refresh</v-icon>
                                        Sync attributes
                                    </v-btn>
                                    <v-btn rounded color="primary" @click="attributeADD(entityType,entityName)" dark>
                                        ADD ATTRIBUTE
                                    </v-btn>
                                </v-row>
                            </v-col>
                        </v-layout>

                        <v-layout
                                v-else-if="value._type == 'org.uengine.modeling.model.Aggregate'"
                                flat>
                            <v-col>
                                <v-data-table
                                        :headers="headers"
                                        :items="value.aggregateRoot.fieldDescriptors"
                                        hide-default-header
                                        hide-default-footer
                                        class="elevation-1"
                                >

                                    <template v-slot:item.action="{ item }">
                                        <v-icon
                                                v-if="!item.isKey"
                                                small
                                                @click="deleteEntity(item)"
                                        >
                                            delete
                                        </v-icon>
                                    </template>
                                </v-data-table>


                                <v-row justify="center">
                                    <v-flex xs4>
                                        <v-select v-model="entityType" :items="entityTypeList" label="Type"
                                                  style="margin-right: 15px;"></v-select>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-text-field v-model="entityName" label="Name"
                                                      v-on:keyup.enter="attributeADD(entityType,entityName)"
                                                      required></v-text-field>
                                    </v-flex>
                                </v-row>
                                <v-row justify="end">
                                    <v-btn rounded color="primary" @click="attributeADD(entityType,entityName)" dark>
                                        ADD ATTRIBUTE
                                    </v-btn>
                                </v-row>
                            </v-col>
                        </v-layout>

                        <v-layout v-if="value._type == 'org.uengine.modeling.model.Command'">
                            <v-autocomplete v-model="value.restfulType" :items="restfulList"
                                            label="Restful TYPE" persistent-hint>
                            </v-autocomplete>
                        </v-layout>


                        <span class="headline"
                              v-if="value._type == 'org.uengine.modeling.model.Event' || value._type == 'org.uengine.modeling.model.Policy' || value._type == 'org.uengine.modeling.model.Command' || value._type == 'org.uengine.model.External'">Associated Aggregate</span>
                        <v-layout
                                v-if="value._type == 'org.uengine.modeling.model.Event' || value._type == 'org.uengine.modeling.model.Policy' || value._type == 'org.uengine.modeling.model.Command' || value._type == 'org.uengine.model.External'"
                                flat>
                            <v-col>
                                <v-autocomplete
                                        v-model="value.aggregate"
                                        style="margin-left: 20px; margin-right: 20px;"
                                        :items="aggregateList"
                                        item-text="name"
                                        return-object
                                        label="Select Aggregate" persistent-hint>
                                </v-autocomplete>
                            </v-col>
                        </v-layout>
                        <v-layout
                                v-if="value._type == 'org.uengine.modeling.model.View'"
                        >
                            <v-card-text>
                                <span class="headline">Type</span>
                                <v-layout>
                                    <v-col>
                                        <v-autocomplete
                                                v-model="value.aggregate"
                                                :items="aggregateList"
                                                item-text="name"
                                                return-object
                                                label="Select Aggregate" persistent-hint>
                                        </v-autocomplete>

                                        <v-radio-group
                                                v-model="value.dataProjection"
                                                style="padding-left: 10px; " row>
                                            <v-radio label="CQRS" value="cqrs"></v-radio>
                                            <v-radio label="UI Mashup" value="uimashup"></v-radio>
                                            <v-radio disabled label="GraphQL" value="graphql"></v-radio>
                                        </v-radio-group>

                                        <v-data-table
                                                v-if="value.dataProjection"
                                                v-model="value.viewFieldDescriptors"
                                                :headers="value.viewHeaders"
                                                :items="value.viewFieldDescriptors"
                                                class="elevation-1"
                                                align="center"
                                                style="font-size: medium"

                                        >
                                            <template v-slot:item.isKey="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-checkbox v-model="item.isKey" type="text"></v-checkbox>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{item.isKey}}
                                                </div>
                                            </template>

                                            <template v-slot:item.name="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-text-field v-model="item.name" label="columnName"></v-text-field>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{item.name}}
                                                </div>
                                            </template>

                                            <template v-slot:item.className="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-text-field v-model="item.className" label="Type"></v-text-field>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{item.className}}
                                                </div>
                                            </template>

                                            <!-- CQRS -->
                                            <template v-slot:item.sourceEvent="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-autocomplete v-model="item.sourceEvent"
                                                                    item-text="name"
                                                                    return-object
                                                                    :items="eventList"
                                                                    label="Event List"></v-autocomplete>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{ item.sourceEvent.name }}
                                                </div>
                                            </template>
                                            <template v-slot:item.eventDirectValue="{ item }">
                                                <div v-if="value.editingView">

                                                    <v-autocomplete v-model="item.eventDirectValue"
                                                                    item-text="name"
                                                                    item-value="name"
                                                                    :items="item.sourceEvent.fieldDescriptors"
                                                                    label="eventDirectValue"></v-autocomplete>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{ item.eventDirectValue }}
                                                </div>
                                            </template>

                                            <template v-slot:item.viewColumnName="{ item }">
                                                <div v-if="value.editingView ">
                                                    <v-select v-model="item.viewColumnName" item-text="name"
                                                              :items="value.viewFieldDescriptors"
                                                              label="viewColumnName"></v-select>
                                                </div>
                                                <div v-if="!value.editingView && item.viewColumnName.length < 1">
                                                    {{value.name}}
                                                </div>
                                                <div v-else-if="!value.editingView">
                                                    {{ value.name + '.'+item.viewColumnName }}
                                                </div>

                                            </template>

                                            <template v-slot:item.sourceEventColumn="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-select v-model="item.sourceEventColumn" item-text="name"
                                                              item-value="name"
                                                              :items="item.sourceEvent.fieldDescriptors"
                                                              label="sourceEventColumn"></v-select>
                                                </div>
                                                <div v-if="!value.editingView && item.sourceEventColumn.length < 1 && item.sourceEvent.name.length > 0">
                                                    {{ item.sourceEvent.name }}
                                                </div>
                                                <div v-else-if="!value.editingView ">
                                                    {{ item.sourceEvent.name + '.'+item.sourceEventColumn }}
                                                </div>
                                            </template>

                                            <!-- UI Mashup -->
                                            <template v-slot:item.repositoryDirectValue="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-text-field v-model="item.repositoryDirectValue"
                                                                  label="RepositoryDirectValue"></v-text-field>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{ item.repositoryDirectValue }}
                                                </div>
                                            </template>
                                            <template v-slot:item.sourceRepository="{ item }">
                                                <div v-if="value.editingView">
                                                    <v-select v-model="item.sourceRepository" item-text="name"
                                                              :items="aggregateList"
                                                              label="sourceRepository"></v-select>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{ item.sourceRepository }}
                                                </div>
                                            </template>
                                            <template v-slot:item.hateoas="{ item }">
                                                <div v-if="value.editingView && item.link.length < 1">
                                                    <v-text-field v-model="item.hateoas" label="hateoas"></v-text-field>
                                                </div>
                                                <div v-else-if="value.editingView && item.link.length > 0">
                                                    <v-text-field disabled v-model="item.hateoas"
                                                                  label="hateoas"></v-text-field>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{ item.hateoas }}
                                                </div>
                                            </template>

                                            <template v-slot:item.link="{ item }">
                                                <div v-if="value.editingView && item.hateoas.length < 1">
                                                    <v-text-field v-model="item.link" label="link"></v-text-field>
                                                </div>
                                                <div v-else-if="value.editingView && item.hateoas.length > 0">
                                                    <v-text-field disabled v-model="item.link"
                                                                  label="link"></v-text-field>
                                                </div>
                                                <div v-if="!value.editingView">
                                                    {{ item.link }}
                                                </div>
                                            </template>

                                            <template v-slot:item.action="{ item }">
                                                <v-icon
                                                        medim
                                                        @click="viewdeleteItem(item)"
                                                >
                                                    delete
                                                </v-icon>
                                            </template>


                                            <template v-slot:body.append>
                                                <v-row>
                                                    <v-btn text v-if="value.editingView == false"
                                                           @click="viewEntityEdit()"> EDIT
                                                    </v-btn>
                                                    <v-btn text v-if="value.editingView == true" @click="viewRowAdd()">
                                                        <v-icon>mdi-plus</v-icon>
                                                    </v-btn>
                                                    <v-btn text v-if="value.editingView == true "
                                                           @click="viewEntitySave()"> SAVE
                                                    </v-btn>
                                                </v-row>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-layout>
                            </v-card-text>

                        </v-layout>
                        <v-layout v-else-if="value._type == 'org.uengine.modeling.model.Aggregate'">
                            <v-divider dark style="margin-top: 10px; margin-bottom: 10px;"></v-divider>
                            <v-btn block color="info" rounded @click="umlDiagramOpen()"> Edit Domain Model by UML
                            </v-btn>
                        </v-layout>

                    </v-card-text>
                </v-card>
            </v-list>
        </v-navigation-drawer>
    </v-layout>
</template>

<script>
    var _ = require('lodash')
    var googleTranslate = require('google-translate')(process.env.VUE_APP_TRANSLATE_KEY);
    var changeCase = require('change-case');
    var pluralize = require('pluralize');
    //https://www.npmjs.com/package/change-case

    export default {
        name: 'modeling-property-panel',
        props: {
            value: Object,
            titleName: String,
            img: String,
        },
        computed: {
            fieldDescriptors: function () {
                var me = this
                return me.value.fieldDescriptors
            },
            widthScale: function () {
                var me = this;
                if (me.value._type == 'org.uengine.modeling.model.View') {
                    return 500
                } else {
                    return 390
                }
            },
            eventList: function () {
                var designer = this.$parent.getComponent('modeling-designer')

                var tmp = []

                designer.value.definition.forEach(function (tmpData) {
                    if (tmpData != null) {
                        if (tmpData._type == 'org.uengine.modeling.model.Event')
                            tmp.push(tmpData)
                    }

                })

                tmp = tmp.filter(n => n)
                return tmp
            },
            aggregateList: function () {
                var designer = this.$parent.getComponent('modeling-designer')

                var tmp = []

                designer.value.definition.forEach(function (tmpData) {
                    if (tmpData != null) {
                        if (tmpData._type == 'org.uengine.modeling.model.Aggregate') {
                            var ob = {
                                'id': tmpData.elementView.id,
                                'name': tmpData.name,
                                'namePlural': tmpData.namePlural,
                                'namePascalCase': tmpData.namePascalCase,
                                'nameCamelCase': tmpData.nameCamelCase,
                                'boundedContext': tmpData.boundedContext,
                                'fieldDescriptors': tmpData.aggregateRoot['fieldDescriptors']
                            }
                        }
                        tmp.push(ob)
                    }
                })

                tmp = tmp.filter(n => n)
                return tmp
            },
        },
        data: function () {
            return {
                show: false,
                navigationDrawer: false,
                _item: this.value,
                preventWatch: false,
                x: null,
                y: null,
                width: null,
                height: null,
                style: [],
                angle: null,
                connectedList: [],
                connectedListName: {},
                viewList: null,
                restfulList: ['GET', 'POST', 'PATCH', 'DELETE'],
                entityTypeList: ['Integer', 'String', 'Float', 'Double', 'Long'],
                entityType: '',
                entityName: '',
                headers: [
                    {
                        text: 'type',
                        value: 'className',
                        align: 'center'
                    },
                    {
                        text: 'name',
                        value: 'name',
                        align: 'center'
                    },
                    {
                        text: 'Actions',
                        value: 'action',
                        sortable: false,
                        align: 'center'
                    },],
                translateText: '',
                usedTranslate: false,
                UMLInput: '',
                editedIndex: '',

            }
        },
        created: function () {

        },
        mounted: function () {
        },
        watch: {
            "value.relationValue": function (newVal, oldVal) {
                var me = this
                var st = "create"
                if (oldVal)
                    st = "modify"



                var poObj={
                    state: st,
                    element:me.value

                }

                me.$EventBus.$emit(`${me.value.sourceElement.elementView.id}`, poObj)
                me.$EventBus.$emit(`${me.value.targetElement.elementView.id}`, poObj)


            },
            "value.relationKey": function (newVal) {
                var me = this

                // me.value.relationValue = newVal
                if (newVal == 'RestAPI') {
                    me.value.relationValue = 'Pub/Sub'
                } else {
                    //restful
                }


            },
            "value.name": function (newVal, oldVal) {
                var me = this
                if (oldVal == 'tmp') {
                    me.getTranslate(newVal)
                    me.initNameCase(newVal)
                    me.translateText = '';
                } else {
                    me.getTranslate(newVal)
                    me.setNameCase(newVal)
                    me.translateText = '';
                }
            },
            "value.drawer": function (val) {
                this.navigationDrawer = val;
            },
            //      .
            navigationDrawer: {
                handler: function (val, oldval) {
                    var opengraph = this.$parent.getComponent('opengraph')
                    if (this.titleName == 'Aggregate') {
                        // this.getRelation()
                    }
                    if (val == true) {
                        this._item = this.value;

                        if (this.value.elementView) {
                            this.x = this.value.elementView.x;
                            this.y = this.value.elementView.y;
                            this.width = this.value.elementView.width;
                            this.height = this.value.elementView.height;
                        }

                        this.$emit('update:drawer', val);

                    } else {
                        // 
                        if (this.value._type != 'org.uengine.modeling.model.Policy') {
                            this.value.name = this.value.name.replace(/ /gi, "")
                        }
                        //  .
                        this.$emit('update:drawer', false);
                    }
                }
            },

        },
        methods: {
            arrayDiff(a1, a2) {

                var a = [], diff = [];

                for (var i = 0; i < a1.length; i++) {
                    a[a1[i]] = true;
                }

                for (var i = 0; i < a2.length; i++) {
                    if (a[a2[i]]) {
                        delete a[a2[i]];
                    } else {
                        a[a2[i]] = true;
                    }
                }

                for (var k in a) {
                    diff.push(k);
                }

                return diff;
            },
            syncFromAggregate() {
                var me = this
                var arr2 = me.value.aggregate.fieldDescriptors
                var arr1 = me.value.fieldDescriptors

                arr2.forEach(function (agg) {
                    var check = false
                    arr1.forEach(function (ev) {
                        if (JSON.stringify(Object.entries(agg).sort()) === JSON.stringify(Object.entries(ev).sort())) {
                            check = true
                        }
                    })
                    if (!check) {
                        me.value.fieldDescriptors.push(agg)
                    }
                })
                // let difference = arr1
                //     .filter(x => !arr2.includes(x))
                //     .concat(arr2.filter(x => !arr1.includes(x)));
                //
                // // console.log(difference)

            },
            viewEntityEdit() {
                var me = this
                me.value.editingView = true
            },
            viewEntitySave() {
                var me = this
                me.value.editingView = false
                // if(me.dataProjection == 'cqrs'){
                //     me.value.viewFieldDescriptors = me.value.fieldDescriptorsCqrs
                // }else if(me.dataProjection == 'uimashup'){
                //     me.value.viewFieldDescriptors = me.value.fieldDescriptorsMash
                // }else if(me.dataProjection == 'graphql'){
                //     // me.value.viewFieldDescriptors = me.value.fieldDescriptorsGraph
                // }else{
                //     //null
                //
                // }
            },
            viewdeleteItem(item) {
                var me = this
                const index = me.value.viewFieldDescriptors.indexOf(item)
                // confirm('Are you sure you want to delete this item?')
                confirm('Are you sure you want to delete this item?') && me.value.viewFieldDescriptors.splice(index, 1)
            },
            viewRowAdd() {
                var me = this
                if (me.value.dataProjection == 'cqrs') {
                    var ob = {
                        isKey: false,
                        name: '',
                        className: '',
                        sourceEvent: {},
                        eventDirectValue: '',
                        viewColumnName: '',
                        sourceEventColumn: ''
                    }
                    me.value.viewFieldDescriptors.push(ob);

                } else if (me.value.dataProjection == 'uimashup') {
                    var ob = {
                        isKey: false,
                        name: '',
                        className: '',
                        sourceRepository: '',
                        repositoryDirectValue: '',
                        hateoas: '',
                        link: ''
                    }
                    me.value.viewFieldDescriptors.push(ob);
                }
            },
            save() {
                var me = this

                if (this.editedIndex > -1) {
                    Object.assign(me.value.fieldDescriptors[this.editedIndex], this.editedItem)
                } else {
                    me.value.fieldDescriptors.push(this.editedItem)
                }
                me.value.editingView = false
            },
            editItem(item) {
                var me = this
                me.editedIndex = me.value.fieldDescriptors.indexOf(item)

                // this.editedItem = Object.assign({}, item)
                // me.value.editingView  = true
            },
            setNameCase: _.debounce(
                function (newVal) {
                    var me = this
                    if (me.value._type == 'org.uengine.modeling.model.View' || me.value._type == 'org.uengine.modeling.model.Event' || me.value._type == 'org.uengine.modeling.model.Command' || me.value._type == 'org.uengine.modeling.model.Policy' || me.value._type == 'org.uengine.modeling.model.Aggregate') {
                        me.value.namePascalCase = changeCase.pascalCase(newVal)
                        me.value.nameCamelCase = changeCase.camelCase(newVal)
                        me.value.namePlural = pluralize(newVal);
                    }
                }, 200
            ),
            initNameCase:
                function (newVal) {
                    var me = this

                    if (me.value._type == 'org.uengine.modeling.model.View' || me.value._type == 'org.uengine.modeling.model.Event' || me.value._type == 'org.uengine.modeling.model.Command' || me.value._type == 'org.uengine.modeling.model.Policy' || me.value._type == 'org.uengine.modeling.model.Aggregate') {
                        me.value.namePascalCase = changeCase.pascalCase(newVal)
                        me.value.nameCamelCase = changeCase.camelCase(newVal)
                        me.value.namePlural = pluralize(newVal);
                    }
                },
            getTranslate:
                _.debounce(
                    function (newVal) {
                        var me = this
                        if (me.value._type == 'org.uengine.modeling.model.Event') {
                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = true
                                            me.translateText = changeCase.pascalCase(translation.translatedText);
                                        });
                                    }
                                });
                            } catch (e) {
                                // console.log('[Event ERROR]' + e)
                            }

                        } else if (me.value._type == 'org.uengine.modeling.model.Policy') {

                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = false
                                            me.translateText = changeCase.pascalCase(translation.translatedText);
                                        });
                                    } else if (detection.language == 'en') {
                                        me.usedTranslate = false
                                        me.translateText = changeCase.pascalCase(newVal)
                                    }
                                });
                            } catch (e) {
                                // console.log('[Policy ERROR]' + e)
                            }

                        } else if (me.value._type == 'org.uengine.modeling.model.Aggregate') {

                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = true
                                            me.translateText = changeCase.pascalCase(translation.translatedText);
                                        });
                                    } else if (detection.language == 'en') {
                                        me.usedTranslate = true
                                        me.translateText = changeCase.pascalCase(newVal)
                                    }
                                });
                            } catch (e) {
                                // console.log('[Aggregate ERROR]' + e)
                            }

                        } else {
                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = true
                                            me.translateText = translation.translatedText;
                                        });
                                    } else if (detection.language == 'en') {
                                        me.usedTranslate = true
                                    }
                                });
                            } catch (e) {
                                // console.log(e)
                            }

                        }
                    }, 500
                ),
            umlDiagramOpen() {
                var me = this
                // console.log("umlDiagram-Open")
                me.$ModelingBus.$emit('umlDiagram');
            }
            ,
            deleteEntity(val) {
                var me = this

                if (me.value._type == 'org.uengine.modeling.model.Aggregate') {
                    me.value.aggregateRoot.fieldDescriptors.forEach(function (element, idx) {
                        if (element.name == val.name && element.type == val.type) {

                            if (idx > -1)
                                me.value.aggregateRoot.fieldDescriptors.splice(idx, 1)

                            // me.$emit('update:aggregateRoot.fieldDescriptors', me.aggregateRoot.fieldDescriptors);
                        }
                    })
                } else {

                    me.value.fieldDescriptors.forEach(function (element, idx) {
                        if (element.name == val.name && element.type == val.type) {

                            if (idx > -1)
                                me.value.fieldDescriptors.splice(idx, 1)

                            // me.$emit('update:fieldDescriptors', me.fieldDescriptors);
                        }
                    })

                }
            }
            ,
            changeTranslate() {
                this.value.name = this.translateText
                this.usedTranslate = false
            }
            ,
            attributeADD: function (type, name) {
                var me = this
                if (type.length < 1) {
                    type = 'String'
                }
                if (type.length != 0 && name.length != 0) {
                    let tmpObject = null

                    tmpObject = {
                        "_type": "org.uengine.model.FieldDescriptor",
                        "name": name,
                        "namePascalCase": changeCase.pascalCase(name),
                        "nameCamelCase": changeCase.camelCase(name),
                        "className": type,
                    }

                    if (name == 'id' && type == 'Long') {
                        tmpObject = {
                            "_type": "org.uengine.model.FieldDescriptor",
                            "name": name,
                            "namePascalCase": changeCase.pascalCase(name),
                            "nameCamelCase": changeCase.camelCase(name),
                            "className": type,
                            "isKey": true
                        }
                    }

                    if (me.value._type == "org.uengine.modeling.model.Aggregate") {
                        me.value.aggregateRoot.fieldDescriptors.push(tmpObject);
                    } else {
                        me.value.fieldDescriptors.push(tmpObject);

                    }

                    this.entityType = ""
                    this.entityName = ""

                } else {
                    var designer = this.$parent.getComponent('modeling-designer')
                    designer.text = "TYPE & NAME INPUT REQUEST"
                    designer.snackbar = true
                }
            }
            ,

        }
    }
</script>


